.header {
  height: 800px;
}

.wrapper-nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav-container {
  width: 100%;
  z-index: 2;
  background-color: rgba(8, 8, 8, .3);
  position: fixed;
  box-shadow: 0 1px rgba(255, 255, 255, .2);
}

.list-nav {
  display: flex;
}

.item-nav {
  position: relative;
}

.item-nav:not(:last-child) {
  margin-right: 37px;
}

.link-nav {
  color: #fff;
  text-transform: uppercase;
  padding-top: 44px;
  padding-bottom: 44px;
  font-family: droid-sans, sans-serif;
  font-size: 13px;
  font-weight: 400;
  display: inline-block;
}

.link-nav:before {
  content: "";
  width: 100%;
  height: 2px;
  opacity: 0;
  background-color: #4bcaff;
  transition: opacity .25s linear;
  display: block;
  position: absolute;
  top: 0;
}

.link-nav.active {
  color: #4bcaff;
  opacity: 1;
  font-weight: 700;
  transition: opacity .25s linear;
}

.link-nav.active:before {
  content: "";
  width: 100%;
  height: 2px;
  opacity: 1;
  background-color: #4bcaff;
  transition: opacity .25s linear;
  display: block;
  position: absolute;
  top: 0;
}

.wrapper-burger {
  display: none;
}

.slider-wrapper {
  height: 800px;
  position: relative;
  overflow: hidden;
}

.slides-container {
  height: calc(100vh - 60px);
  width: 100%;
  height: 800px;
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  overflow: scroll;
}

.slides-container::-webkit-scrollbar {
  display: none;
}

.wrapper-amount {
  display: flex;
  position: absolute;
  bottom: 15px;
  left: 50%;
  -webkit-transform: translate(-50%);
  transform: translate(-50%);
}

.amount-slides {
  width: 10px;
  height: 10px;
  border: 1px solid #fff;
  border-radius: 50%;
}

.amount-slides:not(:last-child) {
  margin-right: 10px;
}

.amount-slides.active-slide {
  width: 13px;
  height: 13px;
  background-color: #4bcaff;
  border: 1px solid #4bcaff;
}

.slide-arrow {
  cursor: pointer;
  border: none;
  margin: auto;
  padding: 0;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
}

.slide-arrow:hover, .slide-arrow:focus {
  opacity: 1;
}

#slide-arrow-prev {
  z-index: 999;
  width: 40px;
  height: 40px;
  background-color: rgba(75, 201, 255, .2);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s linear;
  left: 70px;
}

#slide-arrow-prev:hover {
  background-color: #4bcaff;
  transition: background-color .25s linear;
}

#slide-arrow-prev:hover .arrow-icon-prev, #slide-arrow-prev:hover .arrow-icon-next {
  fill: #414042;
  transition: fill .25s linear;
}

#slide-arrow-prev:focus {
  background-color: #4bcaff;
}

#slide-arrow-prev:focus .arrow-icon-prev, #slide-arrow-prev:focus .arrow-icon-next {
  fill: #414042;
}

#slide-arrow-next {
  z-index: 999;
  width: 40px;
  height: 40px;
  background-color: rgba(75, 201, 255, .2);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: background-color .25s linear;
  right: 70px;
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

#slide-arrow-next:hover {
  background-color: #4bcaff;
  transition: background-color .25s linear;
}

#slide-arrow-next:hover .arrow-icon-prev, #slide-arrow-next:hover .arrow-icon-next {
  fill: #414042;
  transition: fill .25s linear;
}

#slide-arrow-next:focus {
  background-color: #4bcaff;
}

#slide-arrow-next:focus .arrow-icon-prev, #slide-arrow-next:focus .arrow-icon-next {
  fill: #414042;
}

.slide {
  width: 100%;
  height: 100%;
  flex: 1 0 100%;
}

.slide:nth-child(1) {
  background: url("../img/slide_one.d5c26ea4.jpeg") 0 0 / cover no-repeat;
}

@supports (background: url("../img/slide_one.webp")) {
  .slide:nth-child(1) {
    background: url("../img/slide_one.310d9bab.webp") center / cover no-repeat;
  }
}

.slide:nth-child(2) {
  background-image: url("../img/slide_two.fbeeb0ee.jpeg");
  background-repeat: no-repeat;
  background-size: cover;
}

@supports (background: url("../img/slide_two.webp")) {
  .slide:nth-child(2) {
    background: url("../img/slide_two.773e318c.webp") center / cover no-repeat;
  }
}

.slide:nth-child(3) {
